.advantages-items {
    margin-top: 30px;
}

.advantages__box {
    margin-bottom: 30px;
}

.advantages-item__tittle {
    font-weight: 700;
    font-size: 22px;
    line-height: 27px;
    color: #24A3FF;
}


.advantages-item__text {
    margin-bottom: 30px;
}




@media (min-width: 768px) {
    .advantages-items {
        padding: 0 -30px;
        margin-top: 60px;
    }

    .advantages-items__box {
        display: flex;
        flex-direction: column;
        flex-wrap: wrap;
        width: 50%;
        margin-bottom: 60px;
        padding: 0 30px;
    }
}



